/*
 * Masthead
 *
 * Super small header above the content for site name and short description.
 */
.masthead {
  position: fixed;
  z-index: 10;
  background-color: #fcfcfc;
  width: 100%;
  padding-top:    1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #eee;

  -webkit-box-shadow: 0px 0px 15px #888;
     -moz-box-shadow: 0px 0px 15px #888;
          box-shadow: 0px 0px 15px #888;

  .progress-bar {
    position: fixed;
    top: 0;
    height: 5px;
    background: $color;
  }
}

.masthead-title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
  small {
    font-size: 72%;
    letter-spacing: 0;
  }
  .title {
    color: $color;
  }
  .headicons {
    float: right;
    a {
      color: #505050; 
      text-decoration: none;
      padding-left:  .75rem;
    }
  }
}

@media (max-width: 48em) {
  .masthead-title {
    text-align: center;
    small {
      display: none;
    }
  }
}