/* Related posts */
.related,
.full-tags-list {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.related-posts,
.post-list {
  margin-bottom: 1.75rem;
  padding-left: 0;
  list-style: none;
  h3 {
    margin-top: 0;
  }
  li {
    small {
      font-size: 75%;
      color: #999;
    }
    a:hover {
      color: $color;
      text-decoration: none;  
      small {
        color: inherit;
      }
    }
  }
}

/*
 * Pagination
 *
 * Super lightweight (HTML-wise) blog pagination. `span`s are provide for when
 * there are no more previous or next posts to show.
 */
.pagination {
  overflow: hidden; /* clearfix */
  margin-left: -1rem;
  margin-right: -1rem;
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  color: #ccc;
  text-align: center;
}

/* Pagination items can be `span`s or `a`s */
.pagination-item {
  display: block;
  padding: 1rem;
  border: 1px solid #eee;
  &:first-child {
    margin-bottom: -1px;
  }
}

/* Only provide a hover state for linked pagination items */
a.pagination-item:hover {
  background-color: #f5f5f5;
}

.blog-tags {
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  a {
    margin-left: 0.4rem;
  }
}

.blog-tags-list {
  margin-bottom: 1.25rem;
  a {
    font-family: "PT Sans", Helvetica, Arial, sans-serif;
    margin-right: 1.15rem;
  }
}

.share-buttons {
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

body > footer {
  h6 {
    margin-top: 0;
  }
  margin-top: 2rem;
  margin-bottom: 0.5rem;
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3.25rem;
}

@media (min-width: 30em) {
  .pagination {
    margin: 3rem 0;
  }
  .pagination-item {
    float: left;
    width: 50%;
    &:first-child {
      margin-bottom: 0;
      border-top-left-radius:    4px;
      border-bottom-left-radius: 4px;
    }
    &:last-child {
      margin-left: -1px;
      border-top-right-radius:    4px;
      border-bottom-right-radius: 4px;
    }
  }
  .pagination {
    margin: 3rem 0;
  }
  .pagination-item {
    float: left;
    width: 50%;
    &:first-child {
      margin-bottom: 0;
      border-top-left-radius:    4px;
      border-bottom-left-radius: 4px;
    }
    &:last-child {
      margin-left: -1px;
      border-top-right-radius:    4px;
      border-bottom-right-radius: 4px;
    }
  }
}