.margin-toggle {
  display: none;
}

.right-margin,
.left-margin {
  font-size: 0.7rem;
  width: $margin_size;
  margin-top: 0;
  margin-bottom: 1rem;
  position: relative;
  vertical-align: baseline;
}
.right-margin {
  float: right;
  clear: right;
  margin-right: -($margin_size + 1rem);
  margin-left: 0;
}
li .right-margin {
  margin-right: -($margin_size + 3rem);
}
.left-margin {
  float: left;
  clear: left;
  margin-right: 0;
  margin-left: -($margin_size + 1rem);
}
li .left-margin {
  margin-left: -($margin_size + 3rem);
}

@media (max-width: 88em) {
  label {
    cursor: pointer;
    &.margin-toggle {
      display: inline;
      color: $color;
    }
  }
  .right-margin,
  .left-margin {
    display: none;
  }

  .margin-toggle:checked + .right-margin,
  .margin-toggle:checked + .left-margin {
    display: block;
    float: left;
    clear: both;
    width: 100%;
    margin: 1rem 2rem;  
  }
}