/*
 * Body resets
 *
 * Update the foundational and global aspects of the page.
 */
* {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  font-size: 17px;
  line-height: 1.5;
  font-family: charter, Georgia, Cambria, "Times New Roman", Times, serif;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  color: #515151;
  background-color: #fff;
  -webkit-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
  position: relative;
  min-height: 100vh;
}

/*
 * Messages
 *
 * Show alert messages to users. You may add it to single elements like a `<p>`,
 * or to a parent if there are multiple elements to show.
 */
.message {
  margin-bottom: 1rem;
  padding: 1rem;
  color: #717171;
  background-color: #f9f9f9;
}

/*
 * Container
 *
 * Center the page content.
 */
.container {
  position: relative;
  width: 100%;
  max-width: 28rem;
  padding-left:  1rem;
  padding-right: 1rem;
  margin-left:  auto;
  margin-right: auto;
}

.content {
  margin-top: 6.25rem;
  margin-bottom: 2rem;
  padding-bottom: 2.25rem;
}

/* No `:visited` state is required by default (browsers will use `a`) */
a {
  color: $color;
  text-decoration: none;
  strong {
    color: inherit;
  }
  /* `:focus` is linked to `:hover` for basic accessibility */
  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  font-weight: 400;
  color: #313131;
  letter-spacing: -.025rem;
  margin-bottom: .5rem;
  line-height: 1.25;
  text-rendering: optimizeLegibility;
}

h1 {
  font-size: 2rem;
}
h2 {
  margin-top: 1rem;
  font-size: 1.5rem;
}
h3 {
  margin-top: 1.5rem;
  font-size: 1.25rem;
}
h4, h5 {
  margin-top: 1rem;
  font-size: 1rem;
}
h6 {
  margin-top: 0.925rem;
  font-size: 0.925rem;
}

/* Body text */
p {
  margin-top: 0;
  margin-bottom: 1rem;
}

strong {
  color: #303030;
}

/* Lists */
ul, ol, dl {
  margin-top: 0;
  margin-bottom: 1rem;
}
li {
  margin: 0.3rem 0;
}

dt {
  font-weight: bold;
}
dd {
  margin-bottom: .5rem;
}

hr {
  position: relative;
  margin: 1.5rem 0;
  border: 0;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #fff;
}

code {
  font-family: Menlo, Monaco, "Courier New", monospace;
  padding: .25em .5em;
  font-size: 85%;
  color: #bf616a;
  background-color: #f9f9f9;
  border-radius: 3px;
}

pre {
  font-family: Menlo, Monaco, "Courier New", monospace;
  font-size: .8rem;
  line-height: 1.5;
  display: block;
  margin-top: 0;
  margin-bottom: 1rem;
  padding: 1rem;
  white-space: pre;
  overflow-x: auto;
  word-break: break-all;
  word-wrap: break-word;
  background-color: #f9f9f9;
  code {
    width: 50rem;
    padding: 0;
    font-size: 100%;
    color: inherit;
    background-color: transparent;
  }
  &::-webkit-scrollbar {
    -webkit-appearance: none;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 12px;
    border: 4px solid #f9f9f9;
    background-color: rgba(0, 0, 0, .3);
  }
}

/* Pygments via Jekyll */
.highlight {
  margin-bottom: 1rem;
  border-radius: 4px;
  pre {
    margin-bottom: 0;
    line-height: 1.5;
  }
}

/*
 * Posts and pages
 *
 * Each post is wrapped in `.post` and is used on default and post layouts. Each
 * page is wrapped in `.page` and is only used on the page layout.
 */
.page,
.post,
section {
  margin-bottom: 1.5em;
}

/* Blog post or page title */
.page-title,
.post-title {
  margin-top: 0;
}

/* Meta data line below post title */
.post-date {
  display: block;
  margin-top: -.5rem;
  margin-bottom: 1rem;
  color: #9a9a9a;
}

span.newthought {
  font-variant: small-caps;
  font-size: 1.2em;
  letter-spacing: 0.05rem;
}

span.ipa {
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
}

.mjx-math{
  font-size: 1.1rem;
}

blockquote {
  font-style: italic;
  padding: 0.25rem 1.25rem;
  margin: 1.2rem 0;
  color: #7a7a7a;
  border-left: .25rem solid #e5e5e5;
  p {
    margin-bottom: 0.4rem;
  }
  footer {
    font-size: 0.85rem;
    font-style: normal;
    text-align: right;
    width: 60%;
    margin-left: 40%;
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
  }
}

img {
  display: block;
  max-width: 100%;
  margin-bottom: 0.5rem;
  border-radius: 5px;
}

iframe {
  display: block;
  margin-bottom: 0.5rem;
}

table {
  overflow-x: auto;
  display: table;
  margin-left: auto; 
  margin-right: auto;
  margin-bottom: 1rem;
  width: auto;
  border-spacing: 0px;
  border-top: 2px solid #737373;
  border-bottom: 2px solid #737373;
}

th {
  border-bottom: 1px solid lighten(#737373, 25%);
  padding: 0.65ex 0.5em 0.4ex 0.5em;
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.5;

  &.nocmid {
    border-bottom: none;
  }
}

td {
  padding-left: 0.5em;
  padding-right: 0.5em;
  text-align: left;
  font-size: 1rem;
  line-height: 1.5;
}

tbody tr:first-child td,
      tr:first-child td {
  padding-top: 0.65ex;
}

tbody tr:nth-child(odd) td,
tbody tr:nth-child(odd) th {
  background-color: #f9f9f9;
}

thead tr th.l,
td.l {
  text-align: left !important;
}
thead tr th.c,
td.c {
  text-align: center !important;
}
thead tr th.r,
td.r {
  text-align: right !important;
}

p.tablecaption {
  font-size: 80%;
  text-align: center;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  caption-side: top;
}

figcaption {
  font-size: 80%;
  text-align: center;
  width: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0.3rem;
  margin-bottom: 1rem;
}

@media (min-width: 38em) {
  html {
    font-size: 20px;
  }
  .container {
    max-width: 32rem;
  }
}

@media (min-width: 56em) {
  .container {
    max-width: 36rem;
  }
}